<template>
	<view class="discover">
		<view class="title">
			<view>
				<img class="icon" src="/static/main/menu.png" alt="error">
			</view>
			<view>
				<u-search class="input" :placeholder="input_text" v-model="search_text" :showAction="false"></u-search>
			</view>
			<view>
				<img class="icon" src="/static/main/listen_music.png" alt="error">
			</view>
		</view>
		<view class="nav_box">
			<swiper class="nav_swiper" :indicator-dots="true" :autoplay="true" :interval="3000" :circular="true" :duration="1000">
				<swiper-item>
					<view class="swiper-item"><img :src="nav_images_list[0].pic" alt="error"></view>
				</swiper-item>
				<swiper-item>
					<view class="swiper-item"><img :src="nav_images_list[1].pic" alt="error"></view>
				</swiper-item>
				<swiper-item>
					<view class="swiper-item"><img :src="nav_images_list[2].pic" alt="error"></view>
				</swiper-item>
				<swiper-item>
					<view class="swiper-item"><img :src="nav_images_list[3].pic" alt="error"></view>
				</swiper-item>
				<swiper-item>
					<view class="swiper-item"><img :src="nav_images_list[4].pic" alt="error"></view>
				</swiper-item>
				<swiper-item>
					<view class="swiper-item"><img :src="nav_images_list[5].pic" alt="error"></view>
				</swiper-item>
				<swiper-item>
					<view class="swiper-item"><img :src="nav_images_list[6].pic" alt="error"></view>
				</swiper-item>
			</swiper>
		</view>
		<view class="moddle">
			<scroll-view class="scroll" scroll-y="true">
				<view class="main">
					<view class="icon_list">
						<u-scroll-list>
							<view class="list" v-for="(item, index) in icon_list" :key="index">
								<view class="tp">
									<img :src="item.thumb" alt="error">
								</view>
								<p>{{item.text}}</p>
							</view>
						</u-scroll-list>
					</view>
				</view>
			</scroll-view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				input_text:'请输入关键字',
				search_text:'',
				icon_list:[
					{
						thumb:'/static/main/ranking.png',
						text:'排行榜'
					},
					{
						thumb:'/static/main/daily_recommendation.png',
						text:'每日推荐'
					},
					{
						thumb:'/static/main/FM.png',
						text:'私人FM'
					},
					{
						thumb:'/static/main/music_list.png',
						text:'歌单'
					},
					{
						thumb:'/static/main/listen_book.png',
						text:'有声书'
					},
					{
						thumb:'/static/main/album.png',
						text:'数字专辑'
					},
					{
						thumb:'/static/main/live.png',
						text:'直播'
					},
					{
						thumb:'/static/main/attention_music.png',
						text:'关注新歌'
					},
					{
						thumb:'/static/main/music_home.png',
						text:'歌房'
					},
					{
						thumb:'/static/main/game.png',
						text:'游戏专区'
					},
				],
				// 定义轮播图片集合
				nav_images_list:[],
				
			}
		},
		methods: {
			
		},
		mounted() {
			// 在挂载时获取轮播图片
			uni.request({
				url:uni.getStorageSync('baseUrl')+'/banner',
				method:"GET",
				data:{
					type:1
				},
				success: (res) => {
					// console.log(res);
					this.nav_images_list = res.data.banners;
					// console.log(this.nav_images_list);
				}
			})
		}
	}
</script>

<style lang="less" scoped>
	.discover{
		width: 100vw;
		height: 100vh;
		padding-top: 2vh;
		background: linear-gradient(180deg,papayawhip,white) no-repeat;
		
		.title{
			width: 100%;
			height: 3vh;
			display: flex;
			justify-content: space-around;
			
			.icon{
				width: 3vh;
				height: 3vh;
			}
			
			.input{
				width: 75vw;
				height: 3vh;
			}
		}
		
		.nav_box{
			width: 100vw;
			height: 20vh;
			margin-top: 3vh;
			
			.nav_swiper{
				width: 100%;
				height: 100%;
				
				.swiper-item{
					width: 95%;
					height: 100%;
					margin: 0 auto;
					
					img{
						width: 100%;
						height: 100%;
						border-radius: 10px;
					}
				}
			}
		}
		
		.moddle{
			width: 100vw;
			height: 95vh;
			padding-top: 2vh;
			
			.main{
				width: 100%;
				height: 100%;
				
				.icon_list{
					width: 100%;
					height: 15vh;
					border-bottom:solid 1px lightgrey ;
					
					.list{
						width: 5vh;
						height: 10vh;
						border-radius: 100%;
						margin: 1vh 3vh;
						.tp{
							background-color: pink;
							border-radius: 100%;
							width: 5vh;
							height: 5vh;
							text-align: center;
							img{
								margin-top: 1.5vh;
								width: 3vh;
								height: 3vh;
							}
						}
						
						p{
							text-align: center;
							color: #515151;
							font-size: 5px;
						}
					}
				}
			}
		}
	}
</style>
